Vue.use(Tabbar).use(TabbarItem).use(Badge).use(config).use(wx).use(VideoPlayer);
Vue.config.productionTip = false
Vue.config.debug = true

new Vue({
	router,
	store,
	render: h => h(App)
}).$mount('#app')
